.pro-body{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
    background-color: #082032;
}
h1{
    font-size: 200%;
}
h2{
    margin: 2% 0;
    font-size: 250%;
}
.pro-main{
    display: flex;
    justify-content: center;
    flex: 1 1 0;
    max-width: 1200px;
    margin: 2% auto;
}
.pro-section{
    padding-right: 1.5%;
}

.pro-aside{
    width: 13.25%;
    margin-top: 20px;
}
.pro-aside h2{
    font-size: 150%;
}
 .pro-card{
    border-style: solid;
    border-width: 1px;
    border-radius: 10px;
    padding: 10px;
    flex: 0 0 400px;
 }
 .pro-card img{
    width: 100%
 }
 .pro-aside .pro-card span{
    color:  #b9c6ae;
    font-size: 16px;
    font-family: italic;
 }
 .pro-aside .pro-card:hover{
    background-color: #fff;
    box-shadow: 0 0 20px var(--primary-color);
 }

@media screen and (max-width:768px) {
    .pro-main {
        flex-direction: column;
    }
    .pro-section{
        padding-right:0;
    }
    .pro-aside{
        width: 100%;
        text-align: center;
    }
}

