.menu-col{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.menu{
    position:fixed;
    top: 0;
    right: 0;
    min-width: 20%;
    max-width: 30%;
    max-height: 60%;
    background-color: var(--light);
    overflow: auto;
    padding: .5rem;
    box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
    border-bottom-left-radius: .5rem;
} 
.menu h2{
    font-size: 1.5rem;
    border-bottom: 1px solid var(--dark);
    padding-bottom: .5rem;
    margin: 1rem 0;
}
.menu .close{
    position: absolute;
    top: .5rem;
    right: 0.5rem;
    cursor: pointer;
    color: var(--tertiary);
}
.menu .close:hover{
    text-decoration: underline;
}
.menu-closed {
    position: fixed;
    top: 2%;
    right: 1%;
    font-size: 2rem;
    cursor: pointer;
    background-color: var(--secondary);
    border-radius: 50%;
    width:  35px;
    height: 50px;
}
.menu-col span:hover{
    text-decoration: underline;
    cursor: pointer;
}
.menu-col>:nth-child(2):hover{
    text-decoration: underline;
    cursor: pointer;
}
.menu-col a{
    color: var(--primary);
    cursor: pointer;
    text-decoration: none;
}
.menu-col a:hover{
    text-decoration: underline;
}
@media screen and (max-width:768) {
    .menu{
        position: relative;
        max-width: 100%;
        max-height: auto;
        box-shadow: none;
        background-color: inherit;
    }
    .menu-closed{
        position: relative;
        margin: 0 auto;
    }
}


