.foot-footer{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    line-height: 1.3;
    padding: 2%;
    background-color: #13293d;
    color: #fff;
    width: 100%;
}
.foot-footer div a{
    font-size: x-large;
    margin-right: 20px;
    color: #fff;
}
.foot-footer span a{
    color: #fff;
    text-decoration: none;
    font-size: x-large;
}
.foot-footer span a:hover{
    text-decoration: underline;
}