.nav-header{
    padding: 20px 35px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    top: 0;
    background-color: #13293d;
    color: #fff;
    text-shadow: 0 0 10px rgba(18, 165, 233, 0.912);
}
h1{
    font-size: 200%;
}
h2{
    margin: 2% 0;
    font-size: 250%;
}
.nav-header h1{
    font-weight: bold;
    margin: 0;
    color: #b9c6ae;
}
.nav-navbar ul {
    display: flex;
    justify-content: space-between;
    list-style: none;
    align-items: center;
}
.nav-navbar ul li {
    color: #b9c6ae;
    font-weight: 200;
    padding: 10px 15px;
    font-size: 1.55vw;
}
.nav-header ul a{
    color: #b9c6ae;
    text-decoration: none;
}
.nav-navbar a:hover{
    color: #fff;
    text-decoration: underline;
}
@media screen and (max-width:980px) {
    .nav-header{
        padding-bottom: 0;
        justify-content: center;
        position: relative;
    }
}
@media screen and (max-width:768px) {
    .nav-header {
        flex-direction: column;
        display: none;
    }
    .nav-header h1{
        width: 100%;
        text-align: center;
    }
    .nav-header .nav-navbar ul{
        margin-top: 20px;
        width: 100%;
        justify-content: center;
    }
    .nav-header .nav-navbar ul li{
        font-size: 20px;
    }
}