.flex-row{
    display: flex;
}
.res-title{
    display: flex;
    justify-content: center;
    font-size: 44px;
    padding-bottom: 20px;
    width: 50%;
    border-bottom: 3px solid;
    margin: 0 auto 35px auto;
    color: var(--secondary-color);
    text-align: center;
}
.res-border{
    border-color: var(--tertiary-color);
}
.res-resume{
    width: 90%;
    margin: 5.5%;
    border: 3px solid var(--secondary-color);
    background-color:  #13293d;;
    border-radius: 15px;
    box-shadow: 0 0 10px rgba(18, 165, 233, 0.912);

}
.res-cv{
    margin: 50px auto;
    padding-bottom: 50px;
    width: 80%;
    border-bottom: 1px solid var(--tertiary-color);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
.res-cv h3{
    color: var(--secondary-color);
    font-size: 46px;
    flex: 1 30%;
}
.res-prof{
    flex: 2 70%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.res-front-prof{
    flex: 1 12%;
    margin-right: 20px;
}
.res-back-prof{
    flex: 1 12%;
}
.res-front-prof h4{
    font-size: 24px;
    line-height: 1.5;
    color: var(--secondary-color);
}
.res-front-prof ul{
    color: var(--tertiary-color);
    font-size: 16px;
    margin-left: 30px;
    list-style: circle ;
}

.res-back-prof h4{
    font-size: 24px;
    line-height: 1.5;
    color: var(--secondary-color);
}
.res-back-prof ul{
    color: var(--tertiary-color);
    font-size: 16px;
    margin-left: 30px;
    list-style: circle ;
}

.res-button{
    color:  #b9c6ae;
    background-color: var(--secondary-color);
    border: none;
    padding: 20px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
}
.res-button:hover{
    background-color: var(--tertiary-color);
    color: #fff;
}


