.hero-body{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
    line-height: 1.3;
    min-height: 100vh;
    background-color: #082032;
    padding-top: 60px;
}

.hero-main{
    display: flex;
    flex: 1 1 0;
    max-width: 1200px;
    margin: 2% auto;
}
.hero-section{
    flex: 1 1 0;
    padding-right: 3%;
}
.hero-article h3{
    font-size: 20px;
    margin-bottom: 50px;
}
.hero-article h2{
    font-style: italic;
    font-size: 55px;
    color: #b9c6ae;
    text-shadow: 0 0 10px rgba(18, 165, 233, 0.912);
}
.hero-article p{
    margin: 2% 0;
    font-size: 110%;
}
.hero-article ul{
    margin-left: 5%;
    font-size: 110%;
    list-style: circle;
}
.hero-aside{
    width: 25%;
    margin-top: 60px;
}
.hero-aside h2{
    font-size: 150%;
}
 .intro-card{
    border-style: solid;
    border-width: 1px;
    border-radius: 10px;
    padding: 10px;
    flex: 0 0 400px;
 }
 .intro-card img{
    width: 100%
 }

@media screen and (max-width:768px) {
    .hero-main {
        flex-direction: column;
    }
    .hero-section{
        padding-right:0;
        text-align: center;
    }
    .hero-aside{
        width: 100%;
    }
}